import { message, Modal, notification } from 'ant-design-vue';
import NProgress from 'nprogress';
import appConsts from '@/scripts/app-consts';

let tools = {
    nprogressState: false,
    /**
     * 
     */
    loadingStart() {
        let value = 0.1;
        const time = setInterval(() => {
            if (tools.nprogressState) {
                NProgress.set(1);
                return clearInterval(time);
            }
            value = value + 0.1;
            if (value >= 1) {
                NProgress.set(1);
                return clearInterval(time);
            }
            NProgress.set(value);
        }, 50);
    },
    /**
     * 
     */
    loadingStop() {
        tools.nprogressState = true;
    },
    //
    message(text, type = '') {
        if (type === '') {
            message.success(text);
        } else if (type === '') {
            message.warning(text);
        } else if (type === '') {
            message.error(text);
        } else {
            message.info(text);
        }
    },
    //alert
    alert(text, type, call) {
        if (type === '') {
            Modal.success({
                title: "",
                content: text,
                onOk() {
                    if (call) call();
                }
            });
        } else if (type === '') {
            Modal.warning({
                title: "",
                content: text,
                onOk() {
                    if (call) call();
                }
            });
        } else if (type === '') {
            Modal.error({
                title: "",
                content: text,
                onOk() {
                    if (call) call();
                }
            });
        } else {
            Modal.info({
                title: "",
                content: text,
                onOk() {
                    console.log('call', call);
                    if (call) call();
                }
            });
        }
    },
    //
    confirm(text, successCallBack, cancelCallBack, title = '') {
        Modal.confirm({
            title: title,
            content: text,
            okText: '',
            cancelText: '',
            onOk() {
                if (successCallBack) successCallBack();
            },
            onCancel() {
                if (cancelCallBack) cancelCallBack();
            },
        });
    },
    //
    notice(text, type, title = "") {
        if (type === '') {
            notification.success({
                message: title,
                description: text
            });
        } else if (type === '') {
            notification.warning({
                message: title,
                description: text
            });
        } else if (type === '') {
            notification.error({
                message: title,
                description: text
            });
        } else {
            notification.info({
                message: title,
                description: text
            });
        }
    },
    //fileurl  
    getObjectUrl: function (file) {
        var url = "";
        if (window.createObjectURL != undefined) { // basic
            url = window.createObjectURL(file);
        } else if (window.URL != undefined) { // mozilla(firefox)
            url = window.URL.createObjectURL(file);
        } else if (window.webkitURL != undefined) { // webkit or chrome
            url = window.webkitURL.createObjectURL(file);
        }
        return url;
    },
    // base64
    readFile: function (obj, callBack) {
        var file = obj.files[0];
        var resVal;
        //
        if (!/image\/\w+/.test(file.type)) {
            alert("");
            return false;
        }
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
            //alert(this.result); //base64
            resVal = this.result;
            if (callBack) callBack(resVal);
            //return resVal;
        }

    },
    getCookie: function (name) {
        let reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        let arr = top.document.cookie.match(reg);
        if (arr)
            return unescape(arr[2]);
        else
            return null;
    },
    delCookie: function (name) {
        var exp = new Date();
        exp.setTime(exp.getTime() - 1);
        var cval = tools.getCookie(name);
        if (cval != null)
            top.document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
    },
    //
    //s2020
    //h12h12
    //d30d30
    setCookie: function (name, value, time = 'h12', path = '/') {
        if (!time) time = 'h12';
        var strsec = tools.getSec(time);
        var exp = new Date();
        exp.setTime(exp.getTime() + strsec * 1);
        top.document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString() + (path ? (";path=" + path) : ";path=/");
    },
    getSec: function (str) {
        var str1 = str.substring(1, str.length) * 1;
        var str2 = str.substring(0, 1);
        if (str2 == "s") {
            return str1 * 1000;
        } else if (str2 == "h") {
            return str1 * 60 * 60 * 1000;
        } else if (str2 == "d") {
            return str1 * 24 * 60 * 60 * 1000;
        }
    },
    //
    clearCache(call) {
        localStorage.removeItem('hzyAntdVue-TabsList');
        localStorage.removeItem('hzyAntdVue-HeaderTheme');
        localStorage.removeItem('hzyAntdVue-MenuTheme');
        if (call) call();
    },
    /**
     * @param {Object} json
     * @param {Object} type  ==>;1 ==>;2 ==>
     * jsonkey
     */
    jsonKeysToCase(json, type) {
        if (typeof json == 'object') {
            var tempJson = JSON.parse(JSON.stringify(json));
            toCase(tempJson);
            return tempJson;
        } else {
            return json;
        }

        function toCase(json) {
            if (typeof json == 'object') {
                if (Array.isArray(json)) {
                    json.forEach(function (item) {
                        toCase(item);
                    })
                } else {
                    for (var key in json) {
                        var item = json[key];
                        if (typeof item == 'object') {
                            toCase(item);
                        }
                        delete (json[key]);
                        switch (type) {
                            case 1:
                                //key
                                json[key.toLocaleUpperCase()] = item;
                                break;
                            case 2:
                                //key
                                json[key.substring(0, 1).toLocaleUpperCase() + key.substring(1).toLocaleLowerCase()] = item;
                                break;
                            default:
                                //key
                                json[key.toLocaleLowerCase()] = item;
                                break;
                        }
                    }
                }
            }
        }
    },
    // token
    setAuthorization(token) {
        // this.setCookie(appConsts.tokenKey, token);
        localStorage.setItem(appConsts.tokenKey, token);
    },
    // token
    getAuthorization() {
        // return this.getCookie(appConsts.tokenKey);
        return localStorage.getItem(appConsts.tokenKey);
    },
    // token
    delAuthorization() {
        this.delCookie(appConsts.tokenKey);
    },
    //
    setHeaderTheme(theme) {
        localStorage.setItem("hzyAntdVue-HeaderTheme", theme);
    },
    //
    getHeaderTheme() {
        return localStorage.getItem("hzyAntdVue-HeaderTheme");
    },
    //
    setMenuTheme(theme) {
        localStorage.setItem("hzyAntdVue-MenuTheme", theme);
    },
    // //light|dark //
    getMenuTheme() {
        let theme = localStorage.getItem("hzyAntdVue-MenuTheme");
        return theme ? theme : "dark";
    },
    // topNav
    setTopNav(topNav) {
        localStorage.setItem("hzyAntdVue-TopNav", topNav ? "1" : "0");
    },
    // topNav
    getTopNav() {
        let value = localStorage.getItem("hzyAntdVue-TopNav");
        return value ? (value == "1" ? true : false) : true;
    }

};

export default tools;