import axios from "axios";
import tools from "@/scripts/tools";
import appConsts from '@/scripts/app-consts';
import router from '@/router'

//axios 
axios.defaults.timeout = 100 * 1000;
axios.defaults.baseURL = appConsts.domainName + "/api"; //http://localhost:6789
let isLoading = true;

//http request 
axios.interceptors.request.use(
    (config) => {
        if (isLoading) {
            tools.loadingStart();
        }

        let authorization = tools.getAuthorization();
        if (authorization) {
            config.headers["Authorization"] = authorization;
        }
        config.headers["X-Requested-With"] = "XMLHttpRequest";
        // config.headers['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
        config.headers["Content-Type"] = "application/json; charset=UTF-8";

        if (!config.data) return config;

        if (config.data.isUpload) config.headers["Content-Type"] = "multipart/form-data";
        // else
        //config.data = qs.stringify(config.data); //  

        return config;
    },
    (error) => {
        console.log(error);
        return Promise.reject(error);
    }
);

//http response 
axios.interceptors.response.use(
    (response) => {
        tools.loadingStop();
        const data = response.data;

        if (Object.prototype.hasOwnProperty.call(data, "code")) {
            //      = -2,
            //  = -1,
            //  = 0,
            //  = 1,

            if (data.code === -1) {
                //
                tools.message(data.message + ",!", "");
                return router.push("/login");
            }
            if (data.code === -2) {
                //
                tools.message(data.message, "");
            }
            if (data.code === 0) {
                //
                tools.message(data.message, "");
            }
        }

        return response;
    },
    (error) => {
        tools.loadingStop();

        if (error.response) {

            if (error.response.status === 401) {
                tools.notice("!", "");
                router.replace("/login");
                // window.location.reload();
            }
        }

        if (error.message === 'Network Error') {
            tools.message('!', "");
            router.replace("/login");
        }

        console.log(error);
        
        return Promise.reject(error.response.data);

    }
);

/**
 * get
 * @param url
 * @param data
 * @param loading 
 * @param headers 
 * @returns {Promise}
 */
export function get(url, data = {}, loading = true, config = {}) {
    isLoading = loading;
    config["params"] = data;
    return new Promise((resolve, reject) => {
        axios
            .get(url, config)
            .then((response) => {
                if (response) resolve(response.data);
            })
            .catch((err) => {
                reject(err);
            });
    });
}

/**
 * post
 * @param url
 * @param data
 * @param loading 
 * @param config config
 * @returns {Promise}
 */
export function post(url, data = {}, loading = true, config = {}) {
    isLoading = loading;
    return new Promise((resolve, reject) => {
        axios.post(url, data, config).then(
            (response) => {
                if (response) resolve(response.data);
            },
            (err) => {
                reject(err);
            }
        );
    });
}

/**
 *  post  
 * @param url
 * @param data
 * @param loading 
 * @param config config
 * @returns {Promise}
 */
export function upload(url, data = {}, loading = true, config = {}) {
    isLoading = loading;
    if (!data) data = {};
    data.isUpload = true;
    return new Promise((resolve, reject) => {
        axios.post(url, data, config).then(
            (response) => {
                if (response) resolve(response.data);
            },
            (err) => {
                reject(err);
            }
        );
    });
}

/**
 *  get 
 *
 * @param url
 * @param data
 * @param loading 
 * @returns {Promise}
 */
export function download(url, data = {}, fileName, loading = true) {
    return new Promise((resolve, reject) => {
        post(url, data, loading, { responseType: "blob" }).then(
            (res) => {
                var blob = new Blob([res], { type: res.type });
                if (!fileName) fileName = new Date().getTime();
                if (window.navigator && window.navigator.msSaveOrOpenBlob) {
                    // IE
                    window.navigator.msSaveOrOpenBlob(blob, fileName);
                } else {
                    let url = (window.URL || window.webkitURL).createObjectURL(blob);
                    // window.open(url, "_blank"); //
                    // window.URL.revokeObjectURL(url) // BlobURLblob

                    let a = document.createElement("a");
                    a.style.display = "none";
                    a.href = url;
                    a.setAttribute("download", fileName);
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a); // 
                    // window.location.href = url
                    window.URL.revokeObjectURL(url); // BlobURLblob
                }

                resolve(res);
            },
            (err) => {
                reject(err);
            }
        );
    });
}

