import { post } from '@/scripts/request';
import tools from "@/scripts/tools";

export default {
    /**
     * 
     * 
     * @param {*} userName 
     * @param {*} userPassword 
     */
    login(userName, userPassword) {
        if (!userName) {
            return tools.message("!", "");
        }

        if (!userPassword) {
            return tools.message("!", "");
        }

        return post('account/check', {
            userName,
            userPassword
        });
    }
}